import React, { FunctionComponent, useMemo, useState } from 'react'
import Layout from '../components/layout/layout'
import { graphql } from 'gatsby'
import { MarkdownRemarkConnection, Site } from '@/graphql-types'
import { useTransition, animated } from 'react-spring'
import classnames from 'classnames'
import { PostItem } from '../components/post-item'

const Styles = require('../styles/pages/tags.module.styl')

type Props = {
  data: {
    allMarkdownRemark: MarkdownRemarkConnection
    site: Site
  }
  location: Location
}

const Tags: FunctionComponent<Props> = ({ location, data: { site, allMarkdownRemark } }) => {
  const tags = useMemo<string[]>(() => {
    let _tags = []

    allMarkdownRemark.edges.forEach(edge => {
      _tags = _tags.concat(edge.node.frontmatter.tags)
    })
    return Array.from(new Set(_tags)).sort()
  }, [allMarkdownRemark.edges])

  const [selectedTag, setSelectedTag] = useState(tags[0])

  const posts = useMemo(() => allMarkdownRemark.edges.filter(edge => edge.node.frontmatter.tags.some(tag => tag === selectedTag)), [
    selectedTag,
  ])

  // const transitions = useTransition(posts, post => post.node.fields.slug, {
  //   from: { transform: 'translate3d(200px,0,0)', opacity: 0 },
  //   enter: { transform: 'translate3d(0,0px,0)', opacity: 1 },
  //   leave: { transform: 'translate3d(-200px,0,0)', opacity: 0 },
  //   config: { duration: 150 },
  //   trail: 5,
  // })

  return (
    <Layout location={location} title={site.siteMetadata.title}>
      <div className={Styles['header']}> Tags </div>
      <div className={Styles['line']} />
      <div className={Styles['tags_wrap']}>
        {tags.map(tag => (
          <span
            onClick={() => setSelectedTag(tag)}
            className={classnames(Styles['tag_item'], { [Styles['active']]: selectedTag === tag })}
            key={tag}
          >
            #{tag}
          </span>
        ))}
      </div>
      {posts.map(post => {
        return post.node && <PostItem key={post.node.id} node={post.node} className={Styles['post_item']} />
      })}
    </Layout>
  )
}

export default Tags

export const query = graphql`
  query TagQuery {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark {
      totalCount
      edges {
        node {
          id
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            description
            tags
            title
          }
          fields {
            slug
          }
          fileAbsolutePath
          wordCount {
            words
          }
          timeToRead
        }
      }
    }
  }
`
